<template>
  <div class="app">
    <div class="header">
      <Header />
    </div>

    <div class="sidebar">
      <SideBar />
    </div>

    <div class="content">
      <keep-alive>
        <router-view />
      </keep-alive>
    </div>

    <div class="footer">
      <Footer />
    </div>

    <transition name="fade">
      <WalletModal v-if="showWalletModal" />
    </transition>

    <transition name="fade">
      <SettingModal v-if="showSettingModal" />
    </transition>

     <transition name="modal">
      <NetworkModal v-if="showNetworkModal" />
    </transition>

    <Notification />
  </div>
</template>
<script>
import SideBar from "@/components/common/SideBar";
import Header from "@/components/common/Header";
import Footer from "@/components/common/Footer";
import WalletModal from "@/components/common/WalletModal";
import SettingModal from "@/components/common/SettingModal";
import Notification from "@/components/common/Notification";
import NetworkModal from "@/components/common/NetworkModal";
import { mapActions, mapMutations, mapState } from "vuex";
export default {
  name: "App",
  components: {
    Header,
    Footer,
    SideBar,
    Notification,
    WalletModal,
    SettingModal,
    NetworkModal
  },
  data() {
    return {
      loading: true,
    };
  },
  computed: {
    ...mapState("setting", ["referrer", "theme"]),
    ...mapState("wallet", ["showWalletModal", "showSettingModal", "showNetworkModal"]),
  },
  methods: {
    ...mapActions("wallet", [
      "init",
      "getWhitelistToken",
      "checkConnectWallet",
    ]),
    ...mapMutations("setting", ["SET_REFERRER", "SET_THEME"]),
    ...mapMutations("wallet", ["UPDATE_ACTIVE_TAB"]),
    getMediaPreference() {
      const { theme } = this;
      if (theme) return theme;
      return "dark";
    },
  },
  async mounted() {
    const initUserTheme = this.getMediaPreference();
    this.SET_THEME(initUserTheme);
    await this.checkConnectWallet();
    document.addEventListener("visibilitychange", () => {
      this.UPDATE_ACTIVE_TAB(!document.hidden);
    });

    setTimeout(() => {
      if (window.location.href.substr(-1) === "/") {
        window.history.pushState(
          "page2",
          "Title",
          window.location.href.substr(0, window.location.href.length - 1)
        );
      }
    }, 2000);
  },
};
</script>
