<template>
  <div class="footer">
    <button
      v-if="!account"
      @click="TOGGLE_WALLET_MODAL"
      class="connect-wallet-btn"
    >
      <p class="text-linear">Connect Wallet</p>
    </button>

    <div v-else class="wallet-wrap">
      <button
        class="wallet"
        @click="toggleWalletDetail"
        ref="btn-toggle-wallet-detail"
      >
        <img class="wallet-chain" :src="config.chain[chainId].logo" />
        <div>
          <span class="wallet-address">{{ address }}</span>
          <span class="wallet-balance">
            {{
              $filters.currency(
                $filters.normalizeBalance(nativeTokenBalance, 18),
                4
              )
            }}
            {{ config.chain[chainId].nativeCurrency.symbol }}
          </span>
        </div>
      </button>
    </div>

    <div class="theme-setting">
      <img
        :src="
          theme == 'light'
            ? require('@/assets/media/lightmode-active.svg')
            : require('@/assets/media/lightmode-inactive.svg')
        "
        @click="toggleTheme"
      />
      <img
        :src="
          theme == 'light'
            ? require('@/assets/media/darkmode-inactive.svg')
            : require('@/assets/media/darkmode-active.svg')
        "
        @click="toggleTheme"
      />
    </div>

    <div class="language-setting">
      <img src="@/assets/media/earth.svg" />
      <p style="margin-left: 5px">EN</p>
    </div>

    <div class="menu-icon" @click="toggleWalletDetail">
      <img v-if="!showWalletDetail" src="@/assets/media/menu.svg" />
      <img v-else src="@/assets/media/x-icon.svg" />
    </div>

    <div
      class="wallet-detail"
      :class="{
        'show-wallet-detail': showWalletDetail,
      }"
    >
      <font-awesome-icon
        icon="chevron-left"
        color="#96CF24"
        size="1x"
        class="close-wallet-detail"
        @click="toggleWalletDetail"
      />
      <div class="text-linear">Your Address</div>
      <div
        class="copy-address"
        v-clipboard:copy="account"
        v-clipboard:success="onCopy"
      >
        <span>{{ copyAddress }}</span>
        <img src="@/assets/media/copy.svg" />
      </div>
      <div class="token-balance">
        <span>{{ config.chain[chainId].nativeCurrency.symbol }} Balance</span>
        <span>
          {{
            $filters.currency(
              $filters.normalizeBalance(nativeTokenBalance, 18),
              4
            )
          }}
        </span>
      </div>

      <div class="token-balance">
        <span>LZ Balance</span>
        <span>
          {{ $filters.currency($filters.normalizeBalance(lzBalance, 18)) }}
        </span>
      </div>

      <a
        class="explorer-link"
        :href="`${config.chain[chainId].explorer}/address/${account}`"
        target="_blank"
      >
        <span class="text-linear">View on Explorer</span>
        <img src="@/assets/media/out.svg" />
      </a>

      <button
        class="disconnect-btn"
        @click="disconnectWallet() && toggleWalletDetail()"
      >
        <p class="text-linear">Disconnect Wallet</p>
      </button>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from "vuex";
import { shortenAddress } from "@/helpers/utils";
import config from "@/config";

export default {
  data() {
    return {
      config,
      showWalletDetail: false,
    };
  },
  computed: {
    ...mapState("wallet", [
      "account",
      "web3",
      "connecting",
      "tokens",
      "nativeTokenBalance",
      "chainId",
      "lzBalance",
    ]),
    ...mapState("setting", ["theme"]),
    currentRoutePath() {
      return this.$route.fullPath;
    },
    address() {
      return shortenAddress(this.account);
    },
    copyAddress() {
      const { account } = this;

      return account
        ? `${account.slice(0, 12)}...${account.slice(account.length - 12)}`
        : account;
    },
  },
  methods: {
    ...mapMutations("wallet", ["TOGGLE_WALLET_MODAL"]),
    ...mapMutations("setting", ["UPDATE_SLIPPAGE", "SET_THEME"]),
    ...mapActions("wallet", ["disconnectWallet"]),
    toggleWalletDetail() {
      this.showWalletDetail = !this.showWalletDetail;
    },
    onClose() {
      this.showWalletDetail = false;
    },
    onCopy: function () {
      this.$notify({
        group: "copy",
        title: "Address copied",
        text: `${this.account}`,
      });
    },
    toggleTheme() {
      const { theme } = this;
      if (theme === "light") {
        this.SET_THEME("dark");
      } else {
        this.SET_THEME("light");
      }
    },
  },
  mounted() {},
};
</script>

