<template>
  <div class="modal-mask">
    <div class="modal-wrapper">
      <div class="modal-container">
        <div class="modal-header">
          <span>Setting</span>
          <span @click="TOGGLE_SETTING_MODAL">
            <svg
              width="20"
              height="20"
              viewBox="0 0 20 20"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </span>
        </div>
        <div class="modal-body">
          <div class="setting-section">GLOBAL</div>
          <div class="setting-label">Default Transaction Speed (GWEI)</div>
          <div class="setting-gas">
            <button
              class="btn-setting"
              :class="{ active: gasPrice === gasPriceStandard }"
              @click="UPDATE_GASPRICE(gasPriceStandard)"
            >
              Standard ({{
                $filters.currency(
                  $filters.normalizeBalance(gasPriceStandard, 9),
                  0
                )
              }})
            </button>
            <button
              class="btn-setting"
              :class="{ active: gasPrice === gasPriceFast }"
              @click="UPDATE_GASPRICE(gasPriceFast)"
            >
              Fast ({{
                $filters.currency(
                  $filters.normalizeBalance(gasPriceFast, 9),
                  0
                )
              }})
            </button>
            <button
              class="btn-setting"
              :class="{ active: gasPrice === gasPriceInstant }"
               @click="UPDATE_GASPRICE(gasPriceInstant)"
            >
              Instant ({{
                $filters.currency(
                  $filters.normalizeBalance(gasPriceInstant, 9),
                  0
                )
              }})
            </button>
          </div>
          <div class="divider"></div>
          <div class="setting-section">SWAP &amp; LIMIT ORDER</div>
          <div class="setting-label">Slippage Tolerance</div>
          <div class="setting-slippage">
            <button
              class="btn-setting"
              @click="UPDATE_SLIPPAGE(0.1)"
              :class="{ active: slippage === 0.1 }"
            >
              0.1%
            </button>
            <button
              class="btn-setting"
              @click="UPDATE_SLIPPAGE(0.5)"
              :class="{ active: slippage === 0.5 }"
            >
              0.5%
            </button>
            <button
              class="btn-setting"
              @click="UPDATE_SLIPPAGE(1)"
              :class="{ active: slippage === 1 }"
            >
              1.0%
            </button>
            <input
              :value="slippage"
              @change="(e) => UPDATE_SLIPPAGE(e.target.value)"
              @keypress="onlyNumber"
              @focus="$event.target.select()"
            />%
          </div>
          <div class="warning" v-if="slippage > 5">
            Your transaction may be frontrun
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import BigNumber from "@/helpers/bignumber";

export default {
  name: "SettingModal",
  props: {},
  data() {
    return {};
  },
  computed: {
    ...mapState("wallet", ["account", "tokens"]),
    ...mapState("setting", ["gasPrice", "gasPriceStandard"]),
    slippage: {
      get() {
        return this.$store.state.setting.slippage;
      },
      set(value) {
        this.UPDATE_SLIPPAGE(value);
      },
    },
    gasPriceFast() {
      return BigNumber(this.gasPriceStandard).times(120).div(100).toString();
    },
    gasPriceInstant() {
      return BigNumber(this.gasPriceStandard).times(140).div(100).toString();
    },
  },
  watch: {},
  methods: {
    ...mapActions("wallet", ["saveCustomToken"]),
    ...mapMutations("wallet", ["TOGGLE_SETTING_MODAL"]),
    ...mapMutations("setting", ["UPDATE_SLIPPAGE", "UPDATE_GASPRICE"]),
    onlyNumber($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
        $event.preventDefault();
      }
    },
  },
  mounted() {},
};
</script>

