export const web3 = new Web3(new Web3.providers.HttpProvider(config.chain[56].rpcUrlEzdefiPrivate));
export const web3Binance = new Web3(new Web3.providers.HttpProvider(config.chain[56].rpcUrlEzdefiPrivate));
