const PancakeFactoryV1Instance = new web3.eth.Contract(PancakeFactory, config.chain[56].addresses.pancakeFactoryV1);
const PancakeFactoryV2Instance = new web3.eth.Contract(PancakeFactory, config.chain[56].addresses.pancakeFactoryV2);
const SettlementInstance = new web3.eth.Contract(Settlement, config.chain[56].addresses.settlement);

// update swapxViewAddress every 1 min
let swapxViewAddress;
setSwapxViewAddress();
setInterval(async () => {
  await setSwapxViewAddress();
}, 60000) ;

async function setSwapxViewAddress() {
  const swapxImplAddress = await getSwapxImplAddress();
  const res = await axios({
    url: `${config.apiEndpoint}/swapxinfos/${swapxImplAddress}`,
    method: 'get'
  });

  swapxViewAddress = res.data.swapxViewAddress;
}

export async function getSwapxImplAddress() {
  return swapXProxyContract.methods.swapXImpl().call();
}


export async function getTokenPrice(token) {
  const amountIn = BigNumber(1).shiftedBy(Number(token.decimal));
  const addresses1 = [token.address, config.chain[56].addresses.busd]
  const addresses2 = [token.address, config.chain[56].addresses.wbnb, config.chain[56].addresses.busd]

  const results = await Promise.all([
    pancakeRouter.methods.getAmountsOut(amountIn.toFixed(0), addresses1).call(),
    pancakeRouter.methods.getAmountsOut(amountIn.toFixed(0), addresses2).call()
  ])

  return BigNumber.max(results[0][1], results[1][2]).shiftedBy(-Number(token.decimal));
}

export async function getPairRate(factoryContract, ammFee, tokenA, tokenB) {
  const pairAddress = await factoryContract.methods.getPair(tokenA.address, tokenB.address).call();
  if (pairAddress == config.chain[56].addresses.zero) return 0;

  const PancakePairInstance = new web3.eth.Contract(PancakePair, pairAddress);
  const reserves = await PancakePairInstance.methods.getReserves().call();

  let reserveA, reserveB;
  if (BigNumber(tokenA.address).lt(tokenB.address)) {
    reserveA = BigNumber(reserves._reserve0).shiftedBy(-Number(tokenA.decimal))
    reserveB = BigNumber(reserves._reserve1).shiftedBy(-Number(tokenB.decimal))
  } else {
    reserveA = BigNumber(reserves._reserve1).shiftedBy(-Number(tokenA.decimal))
    reserveB = BigNumber(reserves._reserve0).shiftedBy(-Number(tokenB.decimal))
  }

  if (BigNumber(reserveA).lte(0) || BigNumber(reserveB).lte(0)) return -1;

  return BigNumber(reserveB).times(ammFee).div(10000).div(reserveA).toFixed(0);
}

export async function getBestPairRate(tokenA, tokenB) {
  const results = await Promise.all([
    getPairRate(PancakeFactoryV1Instance, config.ammFee.pancakeV1, tokenA, tokenB),
    getPairRate(PancakeFactoryV2Instance, config.ammFee.pancakeV2, tokenA, tokenB),
  ])

  return BigNumber.max.apply(null, results);
}

export async function getExpectedReturn(fromToken, deskToken, amount, parts, flags) {
  const random = Math.floor(Math.random() * 10) % 10;
  let web3Instance;
  if (random < 10) web3Instance = web3Binance;
  if (random < 6) web3Instance = web3;
  const swapXViewContract = new web3Instance.eth.Contract(SwapXView, swapxViewAddress);

  const { returnAmount, estimateGasAmount, distribution } = await swapXViewContract.methods.getExpectedReturn(fromToken, deskToken, new BN(amount.toFixed(0)), parts, flags).call();
  return { returnAmount, estimateGasAmount, distribution }
}

export async function getExpectedReturnWithGasMulti(tokens, amount, parts, flags, destTokenEthPriceTimesGasPrices) {
  const random = Math.floor(Math.random() * 10) % 10;
  let web3Instance;
  if (random < 10) web3Instance = web3Binance;
  if (random < 6) web3Instance = web3;
  const swapXViewContract = new web3Instance.eth.Contract(SwapXView, swapxViewAddress);

  const { returnAmounts, estimateGasAmount, distribution } = await swapXViewContract.methods.getExpectedReturnWithGasMulti(tokens, new BN(amount.toFixed(0)), parts, flags, destTokenEthPriceTimesGasPrices).call();
  return { returnAmounts, estimateGasAmount, distribution }
}

export async function swapEncodeABI(fromToken, deskToken, amount, minReturn, distribution, trader) {
  const { referrer } = store.state.setting;
  const abiData = swapXContract.methods.swap(fromToken, deskToken, new BN(amount.toFixed(0)), new BN(minReturn.toFixed(0)), distribution, trader).encodeABI();
  return swapXProxyContract.methods.swap(
    fromToken,
    deskToken,
    trader,
    new BN(amount.toFixed(0)),
    new BN(minReturn.toFixed(0)),
    referrer,
    abiData
  ).encodeABI();
}

export async function swapMultiEncodeABI(tokens, amount, minReturn, distribution, trader) {
  const { referrer } = store.state.setting;
  const abiData = swapXContract.methods.swapMulti(tokens, new BN(amount.toFixed(0)), new BN(minReturn.toFixed(0)), distribution, trader).encodeABI();
  return swapXProxyContract.methods.swap(
    tokens[0],
    tokens[tokens.length - 1],
    trader,
    new BN(amount.toFixed(0)),
    new BN(minReturn.toFixed(0)),
    referrer,
    abiData
  ).encodeABI();
}

export async function depositBnbForWbnb() {
  return wbnbContract.methods.deposit().encodeABI();
}

export async function swapEstimateGas(fromToken, deskToken, amount, minReturn, distribution, flags) {
  return swapXProxyContract.methods.swap(fromToken, deskToken, new BN(amount.toFixed(0)), new BN(minReturn.toFixed(0)), distribution, flags).estimateGas();
}

