import Web3 from "web3";
import IERC20 from '@/helpers/abi/IERC20.json';
import config from "@/config";
import BigNumber from "@/helpers/bignumber.js";
import Web3Utils from "web3-utils";

var connectors = {};

Object.keys(config.chain).forEach(chainId => {
  connectors[chainId] = new Web3(new Web3.providers.HttpProvider(config.chain[chainId].rpcurl))
})

const defaultChainId = 56;
const BN = Web3Utils.BN;

export async function getBlockNumber(chainId) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  return web3.eth.getBlockNumber();
}

export async function getBalance(chainId, tokenAddress, account) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  if (tokenAddress === config.chain[chainId].addresses.nativeToken) return web3.eth.getBalance(account);
  const contract = new web3.eth.Contract(IERC20, tokenAddress);
  return contract.methods.balanceOf(account).call()
}

export async function getTokenSymbol(chainId, tokenAddress) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  if (tokenAddress === config.chain[chainId].addresses.nativeToken) return config.chain[chainId].tokens[tokenAddress].symbol;
  const contract = new web3.eth.Contract(IERC20, tokenAddress);

  return contract.methods.symbol().call()
}

export async function getTokenDecimal(chainId, tokenAddress) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  if (tokenAddress === config.chain[chainId].addresses.nativeToken) return 18;
  const contract = new web3.eth.Contract(IERC20, tokenAddress);

  return contract.methods.decimals().call()
}

export async function getTotalSupply(chainId, tokenAddress) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  if (tokenAddress === config.chain[chainId].addresses.nativeToken) return BigNumber(154532785).shiftedBy(18).toString();
  const contract = new web3.eth.Contract(IERC20, tokenAddress);

  return contract.methods.totalSupply().call()
}

export async function getAllowance(chainId, tokenAddress, owner, spender) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  if (tokenAddress === config.chain[chainId].addresses.nativeToken) return 10 ** 99;
  const contract = new web3.eth.Contract(IERC20, tokenAddress);
  const allowance = await contract.methods.allowance(owner, spender).call();

  return allowance;
}

export async function getGasPrice(chainId) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  return web3.eth.getGasPrice();
}

export async function estimateGas(chainId, { from, to, data, value }) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  return web3.eth.estimateGas({
    from,
    data,
    to,
    ...(value && { value: new BN(value.toString()) }),
  });
}

export async function approveEncodeABI(chainId, tokenAddress, spender, amount) {
  const web3 = connectors[chainId] || connectors[defaultChainId];

  const contract = new web3.eth.Contract(IERC20, tokenAddress);
  return contract.methods.approve(spender, new BN(amount.toString())).encodeABI();
}