<script>
import { mapState, mapMutations, mapActions } from "vuex";
import { shortenAddress } from "@/helpers/utils";
import config from "@/config";

export default {
  data() {
    return {
      config,
      showWalletDetail: false,
    };
  },
  computed: {
    ...mapState("wallet", [
      "account",
      "web3",
      "connecting",
      "tokens",
      "nativeTokenBalance",
      "chainId",
      "lzBalance",
    ]),
    ...mapState("setting", ["theme"]),
    currentRoutePath() {
      return this.$route.fullPath;
    },
    address() {
      return shortenAddress(this.account);
    },
    copyAddress() {
      const { account } = this;

      return account
        ? `${account.slice(0, 12)}...${account.slice(account.length - 12)}`
        : account;
    },
  },
  methods: {
    ...mapMutations("wallet", ["TOGGLE_WALLET_MODAL"]),
    ...mapMutations("setting", ["UPDATE_SLIPPAGE", "SET_THEME"]),
    ...mapActions("wallet", ["disconnectWallet"]),
    toggleWalletDetail() {
      this.showWalletDetail = !this.showWalletDetail;
    },
    onClose() {
      this.showWalletDetail = false;
    },
    onCopy: function () {
      this.$notify({
        group: "copy",
        title: "Address copied",
        text: `${this.account}`,
      });
    },
    toggleTheme() {
      const { theme } = this;
      if (theme === "light") {
        this.SET_THEME("dark");
      } else {
        this.SET_THEME("light");
      }
    },
  },
  mounted() {},
};
</script>

