<template>
  <div class="modal-mask">
    <div class="modal-wrapper">
      <div class="modal-container">
        <div class="modal-header">
          <span>Switch Network</span>
          <span @click="TOGGLE_NETWORK_MODAL">
            <svg
              width="20"
              height="20"
              viewBox="0 0 20 20"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </span>
        </div>

        <div class="modal-body">
          <div v-for="cid in Object.keys(config.chain)" :key="cid" :class="{network: true, active: chainId == cid}" @click="switchChain(cid), TOGGLE_NETWORK_MODAL()">
            <img :src="config.chain[cid].logo"/>
            <span>{{config.chain[cid].chainName}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import config from "@/config";

export default {
  name: "NetworkModal",
  data(){
    return {
      config
    }
  },
  computed: {
    ...mapState("wallet", ["account", "chainId"]),
  },
  methods: {
    ...mapActions("wallet", ["switchChain"]),
    ...mapMutations("wallet", ["TOGGLE_NETWORK_MODAL"]),
  },
};
</script>

