import Web3 from "web3";
import config from "@/config";
import LaunchpadxIDO from '@/helpers/abi/LaunchpadxIDO.json';
import LaunchpadxIDOv2 from '@/helpers/abi/LaunchpadxIDOv2.json';
import LaunchpadxTicket from '@/helpers/abi/LaunchpadxTicket.json';
import LaunchpadSharePool from '@/helpers/abi/LaunchpadSharePool.json';
import Web3Utils from "web3-utils";
import {getTokenSymbol, getTokenDecimal} from "@/services/common";
import BigNumber from "@/helpers/bignumber";

var connectors = {};

Object.keys(config.chain).forEach(chainId => {
  connectors[chainId] = new Web3(new Web3.providers.HttpProvider(config.chain[chainId].rpcurl))
})

const defaultChainId = 56;
const BN = Web3Utils.BN;

export async function getIDOv1ByAddress(chainId, idoAddress) {
  const web3 = connectors[chainId];

  const LaunchpadxIDOContract = new web3.eth.Contract(LaunchpadxIDO, idoAddress);

  let [
    idoToken,
    multiplier,
    roundsLength
  ] = await Promise.all([
    LaunchpadxIDOContract.methods.idoToken().call(),
    LaunchpadxIDOContract.methods.MULTIPLIER().call(),
    LaunchpadxIDOContract.methods.roundsLength().call()
  ]);

  let rounds = await Promise.all(
    Array.from(Array(Number(roundsLength)).keys()).map(index => LaunchpadxIDOContract.methods.rounds(index).call())
  )

  rounds = await Promise.all(
    rounds.map(async round => {
      const ptcpTokenSymbol = await getTokenSymbol(
        chainId,
        round.ptcpToken
      );
      const ptcpTokenDecimal = await getTokenDecimal(
        chainId,
        round.ptcpToken
      );

      return { ...round, ptcpTokenDecimal, ptcpTokenSymbol };
    })
  )

  let startBlock = Number.MAX_SAFE_INTEGER;
  let endBlock = 0;
  rounds.forEach(round => {
    if (Number(round.startBlock) < startBlock) startBlock = Number(round.startBlock);
    if (Number(round.endBlock) > endBlock) endBlock = Number(round.endBlock);
  })

  return {
    idoAddress,
    idoToken,
    multiplier,
    rounds,
    startBlock,
    endBlock
  }
}

