export async function getTicketCount() {
  const web3 = connectors[56];

  const BscxTicketContract = new web3.eth.Contract(LaunchpadxTicket, config.chain[56].addresses.bscxTicket);
  const bscxTicketCount = await BscxTicketContract.methods.getTicketCount().call();

  return Number(bscxTicketCount);
}

export async function getBscxTickets(address) {
  const web3 = connectors[56];

  const BscxTicketContract = new web3.eth.Contract(LaunchpadxTicket, config.chain[56].addresses.bscxTicket);
  const ticketIds = await BscxTicketContract.methods.getUserTicketId(address).call();
  const tickets = await Promise.all(ticketIds.map(async id => {
    const ticket = await BscxTicketContract.methods.getTicketFromId(id).call();
    return { ...ticket, id };
  }))

  return tickets;
}

export async function getLzShareInfo(address) {
  const web3 = connectors[56];

  const LaunchpadSharePoolContractV1 = new web3.eth.Contract(LaunchpadSharePool, config.chain[56].addresses.launchpadSharePool);
  const LaunchpadSharePoolContractV2 = new web3.eth.Contract(LaunchpadSharePool, config.chain[56].addresses.launchpadSharePoolv2);
  const LaunchpadSharePoolContractV3 = new web3.eth.Contract(LaunchpadSharePool, config.chain[56].addresses.launchpadSharePoolv3);

  const [
    lockingV1,
    totalLockV1,
    unlockableV1,
    nextUnlockableTimeV1
  ] = await Promise.all([
    LaunchpadSharePoolContractV1.methods.lockingShare(address).call(),
    LaunchpadSharePoolContractV1.methods.getTotalLock(address).call(),
    LaunchpadSharePoolContractV1.methods.unlockableShare(address).call(),
    LaunchpadSharePoolContractV1.methods.nextUnlockableTime(address).call(),
  ])

  const [
    lockingV2,
    totalLockV2,
    unlockableV2,
    nextUnlockableTimeV2
  ] = await Promise.all([
    LaunchpadSharePoolContractV2.methods.lockingShare(address).call(),
    LaunchpadSharePoolContractV2.methods.getTotalLock(address).call(),
    LaunchpadSharePoolContractV2.methods.unlockableShare(address).call(),
    LaunchpadSharePoolContractV2.methods.nextUnlockableTime(address).call(),
  ])

  const [
    lockingV3,
    totalLockV3,
    unlockableV3,
    nextUnlockableTimeV3
  ] = await Promise.all([
    LaunchpadSharePoolContractV3.methods.lockingShare(address).call(),
    LaunchpadSharePoolContractV3.methods.getTotalLock(address).call(),
    LaunchpadSharePoolContractV3.methods.unlockableShare(address).call(),
    LaunchpadSharePoolContractV3.methods.nextUnlockableTime(address).call(),
  ])

  return {
    lockingV1,
    totalLockV1,
    unlockableV1,
    nextUnlockableTimeV1: Number(nextUnlockableTimeV1),
    lockingV2,
    totalLockV2,
    unlockableV2,
    nextUnlockableTimeV2: Number(nextUnlockableTimeV2),
    lockingV3,
    totalLockV3,
    unlockableV3,
    nextUnlockableTimeV3: Number(nextUnlockableTimeV3),
  }
}

