import production from '@/config/production.js';
import staging from '@/config/staging.js';

const configs = { production, staging };
const stage = process.env.VUE_APP_NETWORK || 'production';
const config = configs[stage];
config.env = process.env.VUE_APP_ENV || 'staging';

config.chain[56].addresses.swapxproxy = process.env.VUE_APP_SWAPX_PROXY_ADDRESS;
config.chain[56].addresses.settlement = process.env.VUE_APP_SETTLEMENT_ADDRESS;
config.chain[56].addresses.bscxTicket = process.env.VUE_APP_BSCX_TICKET_ADDRESS;
config.chain[56].addresses.launchpadSharePool = process.env.VUE_APP_LAUNCHPADX_SHARE_POOL;
config.chain[56].addresses.launchpadSharePoolv2 = process.env.VUE_APP_LAUNCHPADX_SHARE_POOL_V2;
config.chain[56].addresses.launchpadSharePoolv3 = process.env.VUE_APP_LAUNCHPADX_SHARE_POOL_V3;

export default config;
