import config from "@/config";

const state = {
  slippage: 0.5,
  customTokens: {},
  referrer: config.defaultReferrer,
  theme: "",
  gasPriceStandard: 5*10**9,
  gasPrice: 5*10**9,
  exchange: "pancake2"
}

const mutations = {
  UPDATE_SLIPPAGE(_state, payload) {
    _state.slippage = Number(payload) || 0.5;
  },
  UPDATE_GASPRICE_STANDARD(_state, payload) {
    _state.gasPriceStandard = payload;
  },
  UPDATE_GASPRICE(_state, payload) {
    _state.gasPrice = payload;
  },
  SAVE_CUSTOM_TOKEN(_state, payload) {
    const { token } = payload;
    _state.customTokens = { ..._state.customTokens, [token.address]: {address: token.address} };
  },
  SET_REFERRER(_state, payload){
    _state.referrer = payload;
  },
  SET_THEME(_state, payload) {
    _state.theme = payload;
    document.documentElement.className = payload;
  },
  SET_EXCHANGE(_state, payload) {
    _state.exchange = payload;
  }
}

const actions = {

}

export default {
  state,
  mutations,
  actions,
  namespaced: true
};
