import Web3 from "web3";
import config from "@/config";
import { getBalance } from "@/services/common";
import WalletConnectProvider from "@walletconnect/web3-provider";
import { getWhitelistToken, sendAnalysisInfo, submitOrder } from "@/helpers/utils";
import BigNumber from "@/helpers/bignumber.js";
import dayjs from "@/helpers/dayjs";
import Vue from 'vue'

const state = {
    web3: null,
    connecting: false,
    account: null,
    showWalletModal: false,
    showSettingModal: false,
    showNetworkModal: false,
    tokens: {},
    lowestGasMode: false,
    isUserChangeMode: false,
    activeTab: true,
    orders: [],
    nativeTokenBalance: 0,
    lzBalance: 0,
    chainId: 56,
    provider: null
};

const getters = {
    activeOrders: _state => {
        return _state.orders.filter(order => !order.canceled && BigNumber(order.filledAmountIn).lt(order.amountIn) && Number(order.deadline) > dayjs().unix())
    },
    expiredOrders: _state => {
        return _state.orders.filter(order => order.canceled || BigNumber(order.amountIn).lte(order.filledAmountIn) || Number(order.deadline) < dayjs().unix() || !!order.amountOut || !!order.transactionHash)
    }
}

const mutations = {
    WALLET_CONNECTING(_state, payload) {
        _state.connecting = payload;
    },
    WALLET_CONNECTED(_state, payload) {

        _state.web3 = payload.web3;
    },
    ACCOUNT_CHANGED(_state, payload) {
        _state.account = payload.account;
    },
    TOGGLE_WALLET_MODAL(_state) {
        _state.showWalletModal = !_state.showWalletModal;
    },
    TOGGLE_SETTING_MODAL(_state) {
        _state.showSettingModal = !_state.showSettingModal;
    },
    TOGGLE_NETWORK_MODAL(_state) {
        _state.showNetworkModal = !_state.showNetworkModal;
    },
    UPDATE_TOKENS(_state, payload) {
        _state.tokens = { ..._state.tokens, ...payload.tokens };
    },
    ADD_CUSTOM_TOKEN(_state, payload) {
        const { token } = payload;
        _state.tokens = { [token.address]: token, ..._state.tokens };
    },
    UPDATE_SWAP_MODE(_state, payload) {
        _state.lowestGasMode = payload;
    },
    TURN_OFF_AUTO_MODE(_state) {
        _state.isUserChangeMode = true;
    },
    UPDATE_ACTIVE_TAB(_state, payload) {
        _state.activeTab = payload;
    },
    ADD_ORDERS(_state, payload) {
        const { orders } = payload;
        _state.orders = [...orders, ..._state.orders];
    },
    REPLACE_ORDERS(_state, payload) {
        const { orders } = payload;
        _state.orders = orders;
    },
    UPDATE_ORDER(_state, payload) {
        const index = _state.orders.findIndex(order => order.hash === payload.hash);

        if (index !== -1) {
            _state.orders.splice(index, 1, payload)
        }
    },
    UPDATE_CHAIN_ID(_state, payload) {
        _state.chainId = payload;
    },
    UPDATE_NATIVE_BALANCE(_state, payload) {
        _state.nativeTokenBalance = payload.nativeTokenBalance;
        _state.lzBalance = payload.lzBalance;
    },
    UPDATE_WALLET_CONNECT_PROVIDER(_state, payload) {
        _state.provider = payload;
    }
};

var interval;

const actions = {
    connectWeb3Wallet: async ({ commit, dispatch }) => {
        const injected = window.kardiachain || window.BinanceChain || window.ethereum
        if (injected) {
            try {
                commit('TOGGLE_WALLET_MODAL');
                commit('WALLET_CONNECTING', true);
                await injected.request({ method: "eth_requestAccounts" });

                const web3 = new Web3(injected);
                const chainId = await web3.eth.getChainId();

                commit('UPDATE_CHAIN_ID', BigNumber(chainId).toString());

                const accounts = await web3.eth.getAccounts();

                commit('WALLET_CONNECTING', false);
                commit('WALLET_CONNECTED', { web3 });
                commit('ACCOUNT_CHANGED', { account: accounts[0] });

                dispatch('updateNativeBalance');
                if (interval) clearInterval(interval);
                interval = setInterval(() => {
                    dispatch('updateNativeBalance');
                }, 6000)

                injected.on('accountsChanged', (accounts) => {
                    commit('ACCOUNT_CHANGED', { account: accounts.length ? accounts[0] : null });
                    dispatch('updateNativeBalance');
                    dispatch('updateSwapTabBalance');
                });

                injected.on('chainChanged', (chainId) => {
                    console.log("chainChanged", BigNumber(chainId).toString())
                    commit('UPDATE_CHAIN_ID', BigNumber(chainId).toString());
                    dispatch('updateNativeBalance');
                });
            } catch (e) {
                commit('WALLET_CONNECTING', false);
            }
        } else {
            alert("Please install compatible Web3 wallet");
        }
    },
    checkConnectWallet: async ({ commit, dispatch }) => {
        const injected = window.ethereum || window.BinanceChain || window.kardiachain

        if (injected) {
            const accounts = await injected.request({ method: 'eth_accounts' })
            if (accounts.length) {
                await injected.request({ method: "eth_requestAccounts" });
                const web3 = new Web3(injected);
                const chainId = await web3.eth.getChainId();
                commit('UPDATE_CHAIN_ID', BigNumber(chainId).toString());

                commit('WALLET_CONNECTED', { web3 });
                commit('ACCOUNT_CHANGED', { account: accounts[0] });

                dispatch('updateNativeBalance');
                if (interval) clearInterval(interval);
                interval = setInterval(() => {
                    dispatch('updateNativeBalance');
                }, 6000)

                injected.on('accountsChanged', (accounts) => {
                    commit('ACCOUNT_CHANGED', { account: accounts.length ? accounts[0] : null });
                    dispatch('updateNativeBalance');
                    dispatch('updateSwapTabBalance');
                });

                injected.on('chainChanged', (chainId) => {
                    console.log("chainChanged", BigNumber(chainId).toString())
                    commit('UPDATE_CHAIN_ID', BigNumber(chainId).toString());
                    dispatch('updateNativeBalance');
                });
            }
        }
    },
    connectWalletConnect: async ({ commit, dispatch }) => {
        try {
            commit('WALLET_CONNECTING', true);
            console.log("connectWalletConnect")
            const provider = new WalletConnectProvider(config.walletconnect.options);

            //  Enable session (triggers QR Code modal)
            await provider.enable();
            console.log("provider enabled")
            const web3 = new Web3(provider);
            const accounts = await web3.eth.getAccounts();
            const chainId = await web3.eth.getChainId();
            commit('UPDATE_CHAIN_ID', chainId);
            commit('UPDATE_WALLET_CONNECT_PROVIDER', provider);

            commit('WALLET_CONNECTING', false);
            commit('TOGGLE_WALLET_MODAL');
            commit('WALLET_CONNECTED', { web3 });
            commit('ACCOUNT_CHANGED', { account: accounts[0] });

            dispatch('updateNativeBalance');
            if (interval) clearInterval(interval);
            interval = setInterval(() => {
                dispatch('updateNativeBalance');
            }, 60000)

            provider.on("accountsChanged", (accounts) => {
                commit('ACCOUNT_CHANGED', { account: accounts.length ? accounts[0] : null });
                dispatch('updateNativeBalance');
            });

            // Subscribe to chainId change
            provider.on("chainChanged", (chainId) => {
                console.log(chainId);
                commit('UPDATE_CHAIN_ID', chainId);
                dispatch('updateNativeBalance');
            });

            // Subscribe to session disconnection
            provider.on("disconnect", (code, reason) => {
                console.log(code, reason);

                commit('ACCOUNT_CHANGED', { account: null });
            });
        } catch (e) {
            console.log(e)
        }
    },
    updateSwapTabBalance: async ({ commit, state }, params) => {
        const { account, tokens, chainId } = state;
        if (!account) return;
        if (chainId != 56) return;

        const array = await Promise.all(
            Object.keys(tokens).filter(address => {
                if (params) return params.addresses && params.addresses.includes(address);

                return true;
            }).map(async address => {
                const balance = await getBalance(chainId, address, account);
                return { ...tokens[address], balance };
            })
        );

        const updatedTokens = {};
        array.forEach(token => {
            updatedTokens[token.address] = token;
        })

        commit('UPDATE_TOKENS', { tokens: updatedTokens });
    },
    sendTransaction: async ({ state }, params) => {
        const {
            value,
            data,
            to,
            gasPrice,
            gas
        } = params;

        const { web3, account } = state;

        return web3.eth.sendTransaction({
            from: account,
            data,
            to,
            ...value && { value },
            ...gas && { gas },
            ...gasPrice && { gasPrice }
        });
    },
    getWhitelistToken: async ({ state, commit, dispatch }) => {
        if (state.tokens[config.chain[56].addresses.lz]) return;
        const tokens = await getWhitelistToken();
        commit('UPDATE_TOKENS', { tokens });
        dispatch('updateSwapTabBalance');
    },
    init: async ({ dispatch }) => {
        await dispatch('checkConnectWallet');
        await dispatch('getWhitelistToken');
    },
    saveCustomToken: async ({ commit }, params) => {
        const { token } = params;

        commit('ADD_CUSTOM_TOKEN', { token });
        commit('setting/SAVE_CUSTOM_TOKEN', { token }, { root: true });
    },
    updateAccount: async ({ commit }, params) => {
        const { account } = params;
        commit('ACCOUNT_CHANGED', { account });
        if (account) sendAnalysisInfo(account);
    },
    signTypedDataV4: ({ state, commit }, {
        maker,
        fromToken,
        toToken,
        amountIn,
        amountOutMin,
        recipient,
        deadline,
        fromTokenSymbol,
        fromTokenDecimal,
        toTokenSymbol,
        toTokenDecimal
    }) => {
        return new Promise((resolve, reject) => {
            const { web3 } = state;

            const typedData = {
                domain: {
                    chainId: 56,
                    name: 'OrderBook',
                    version: '1',
                    verifyingContract: config.chain[56].addresses.settlement,
                },

                // Defining the message signing data content.
                message: {
                    maker,
                    fromToken,
                    toToken,
                    amountIn,
                    amountOutMin,
                    recipient,
                    deadline,
                },
                // Refers to the keys of the *types* object below.
                primaryType: 'Order',
                types: {
                    // TODO: Clarify if EIP712Domain refers to the domain the contract is hosted on
                    EIP712Domain: [
                        { name: 'name', type: 'string' },
                        { name: 'version', type: 'string' },
                        { name: 'chainId', type: 'uint256' },
                        { name: 'verifyingContract', type: 'address' },
                    ],
                    Order: [
                        { name: "maker", type: "address" },
                        { name: "fromToken", type: "address" },
                        { name: "toToken", type: "address" },
                        { name: "amountIn", type: "uint256" },
                        { name: "amountOutMin", type: "uint256" },
                        { name: "recipient", type: "address" },
                        { name: "deadline", type: "uint256" },
                    ]
                },
            };

            web3.currentProvider.sendAsync(
                {
                    method: 'eth_signTypedData_v3',
                    params: [state.account, JSON.stringify(typedData)],
                    from: state.account,
                },
                (err, result) => {
                    if (err) return reject(err);
                    if (result.error) {
                        return reject(result.error);
                    }
                    console.log('TYPED SIGNED:' + JSON.stringify(result.result));
                    console.log(result)

                    // const { v, r, s } = splitSignature(result.result);

                    // const recovered = recoverTypedSignature_v4({
                    //     data: typedData,
                    //     sig: result.result,
                    // });

                    // console.log(recovered, state.account);

                    submitOrder({
                        data: typedData,
                        sig: result.result,
                        fromTokenSymbol,
                        fromTokenDecimal,
                        toTokenSymbol,
                        toTokenDecimal
                    }).then(order => {
                        console.log('order submitted: ', order)
                        const fromTokenAmtConvert = BigNumber(order.amountOutMin).shiftedBy(-Number(order.fromTokenDecimal))
                        const toTokenAmtConvert = BigNumber(order.amountIn).shiftedBy(-Number(order.toTokenDecimal));
                        const rate = toTokenAmtConvert.div(fromTokenAmtConvert).toString()
                        const reverseRate = fromTokenAmtConvert.div(toTokenAmtConvert).toString()

                        const orders = [
                            {
                                ...order,
                                canceled: false,
                                filledAmountIn: 0,
                                rate,
                                reverseRate,
                                filledPercent: BigNumber(0).toFixed(2),
                                deadlineFormated: dayjs.unix(deadline).format("YYYY-MM-DD"),
                            }
                        ];
                        commit('ADD_ORDERS', { orders });

                        resolve(orders)
                    }).catch(e => reject(e))
                }
            );
        })
    },
    updateNativeBalance: async ({ commit, state }) => {
        let { account, chainId } = state;
        if (!account) return;
        if (!chainId) chainId = 56;

        const nativeTokenBalance = await getBalance(chainId, config.chain[chainId].addresses.nativeToken, account);
        const lzBalance = await getBalance(56, config.chain[56].addresses.lz, account);
        commit('UPDATE_NATIVE_BALANCE', { nativeTokenBalance, lzBalance });
    },
    switchChain: async ({ state }, chainId) => {
        let injected = state.provider || window.ethereum || window.BinanceChain || window.kardiachain;

        const chainIdHex = '0x' + Number(chainId).toString(16);

        if (!injected) {
            Vue.notify({
                group: "error",
                title: "Please connect wallet",
                text: "You need connect wallet first"
            })

            return;
        }

        try {
            await injected.request({
                method: 'wallet_switchEthereumChain',
                params: [{ chainId: chainIdHex }],
            });
        } catch (switchError) {
            // This error code indicates that the chain has not been added to MetaMask.
            if (switchError.code === 4902) {
                try {
                    await injected.request({
                        method: 'wallet_addEthereumChain',
                        params: [{
                            chainId: chainIdHex,
                            chainName: config.chain[chainId].chainName,
                            rpcUrls: [config.chain[chainId].rpcurl],
                            nativeCurrency: config.chain[chainId].nativeCurrency,
                            blockExplorerUrls: [config.chain[chainId].explorer],
                            iconUrls: [config.chain[chainId].logo]
                        }],
                    });
                } catch (addError) {
                    // handle "add" error
                    Vue.notify({
                        group: "error",
                        title: "Add network failed",
                        text: "Please try again with your wallet and reload website"
                    })
                }
            }
            // handle other "switch" errors
            else {
                Vue.notify({
                    group: "error",
                    title: "Switch network failed",
                    text: "Please try again with your wallet and reload website"
                })
            }
        }
    },
    requestPermissions: async () => {
        const injected = window.ethereum || window.BinanceChain || window.kardiachain

        injected
            .request({
                method: 'wallet_requestPermissions',
                params: [{ eth_accounts: {} }],
            })
            .then((permissions) => {
                const accountsPermission = permissions.find(
                    (permission) => permission.parentCapability === 'eth_accounts'
                );
                if (accountsPermission) {
                    console.log('eth_accounts permission successfully requested!');
                }
            })
            .catch((error) => {
                if (error.code === 4001) {
                    // EIP-1193 userRejectedRequest error
                    console.log('Permissions needed to continue.');
                } else {
                    console.error(error);
                }
            });
    },
    disconnectWallet: async ({ commit }) => {
        commit('ACCOUNT_CHANGED', { account: null });
    }
};

export default {
    state,
    getters,
    mutations,
    actions,
    namespaced: true
};
