export async function getTokenFromAddress(address) {
  const contract = new web3.eth.Contract(IERC20, address);
  const decimal = Number(await contract.methods.decimals().call());
  const symbol = await contract.methods.symbol().call();
  const name = await contract.methods.name().call();
  const icon = config.customIcon;
  let balance = 0;

  if (store.state.wallet.account) {
    balance = await contract.methods.balanceOf(store.state.wallet.account).call();
  }

  return { contract, decimal, symbol, name, address: Web3Utils.toChecksumAddress(address), icon, balance };
}

export async function getWhitelistToken() {
  const { customTokens } = store.state.setting;
  const tokens = {}

  const res = await axios({
    method: 'get',
    url: 'https://wallet-api.ezdefi.com/api/tokens/extension'
  })

  res.data.filter(token => token.network === 'binance-smart-chain' && Web3Utils.isAddress(token.address)).forEach(token => {
    tokens[Web3Utils.toChecksumAddress(token.address)] = {
      ...token,
      icon: token.logo || `https://s2.coinmarketcap.com@/assets/img/coins/64x64/${token.cmcId}.png`,
      ...token.symbol === 'BSCX' && { icon: '@/assets/media/bscx-icon.svg' },
      address: Web3Utils.toChecksumAddress(token.address)
    };
  })

  for (const address in customTokens) {
    try {
      if (!Web3Utils.isAddress(address)) continue;
      const contract = new web3.eth.Contract(IERC20, address);
      const decimal = Number(await contract.methods.decimals().call());
      const name = await contract.methods.name().call();
      const symbol = await contract.methods.symbol().call();
      const icon = config.customIcon;
      tokens[address] = { contract, decimal, symbol, name, address: Web3Utils.toChecksumAddress(address), icon };

    } catch (e) {
      console.log(e)
      continue
    }
  }

  tokens[config.chain[56].addresses.bnb] = {
    address: config.chain[56].addresses.bnb,
    decimal: 18,
    symbol: "BNB",
    name: "BNB",
    icon: '@/assets/media/bnb.png'
  }

  return tokens;
}

export async function getOrders(params) {
  const res = await axios({
    method: 'get',
    url: `${config.apiEndpoint}/orderbooks`,
    params: {
      ...params,
      sort: "-createdAt"
    }
  })

  const { maker } = params;

  const orders = res.data.rows;

  return Promise.all(orders.map(async order => {
    const canceled = await isOrderCanceled(maker, order.hash);
    const filledAmountIn = await getOrderFilledAmount(order.hash);
    const fromTokenAmtConvert = BigNumber(order.amountOutMin).shiftedBy(-Number(order.fromTokenDecimal))
    const toTokenAmtConvert = BigNumber(order.amountIn).shiftedBy(-Number(order.toTokenDecimal));
    const reverseRate = toTokenAmtConvert.div(fromTokenAmtConvert).toString()
    const rate = fromTokenAmtConvert.div(toTokenAmtConvert).toString()
    const filledPercent = BigNumber(filledAmountIn).times(100).div(order.amountIn).toFixed(2);
    const deadlineFormated = dayjs.unix(order.deadline).format("YYYY-MM-DD");

    return { ...order, canceled, filledAmountIn, rate, reverseRate, filledPercent, deadlineFormated };
  }))
}

