export async function getPairChart({ ammSource, baseToken, quoteToken, resolution, from, to }) {
  if (baseToken.address === config.chain[56].addresses.bnb) baseToken = config.chain[56].tokens[config.chain[56].addresses.wbnb]
  if (quoteToken.address === config.chain[56].addresses.bnb) quoteToken = config.chain[56].tokens[config.chain[56].addresses.wbnb]
  if (baseToken.address === config.chain[56].addresses.busd) {
    baseToken = quoteToken;
    quoteToken = config.chain[56].tokens[config.chain[56].addresses.busd];
  }

  const res = await axios({
    url: `${config.apiChart}/candleline2/${ammSource}/${baseToken.address}/${quoteToken.address}`,
    method: 'get',
    params: {
      r: resolution,
      f: from,
      t: to
    }
  })

  if (res.data.s === "ok") {
    const candleData = []
    for (let i = 0; i < res.data.t.length; i++) {
      candleData.push({
        time: res.data.t[i],
        open: res.data.o[i],
        high: res.data.h[i],
        low: res.data.l[i],
        close: res.data.c[i]
      })
    }

    return candleData;
  }

  return [];
}

