export async function getUpcomingIdos() {
  const res = await axios({
    method: "get",
    url: `${config.apiEndpoint}/upcomingidos`
  })


  return res.data.rows
    .sort((firstEl, secondEl) => dayjs(firstEl.timePublish, "YYYY-MM-DD HH:mm Z").unix() - dayjs(secondEl.timePublish, "YYYY-MM-DD HH:mm Z").unix())
    .filter(it => dayjs(it.timePublish, "YYYY-MM-DD HH:mm Z").unix() > dayjs().unix())
    .map(it => ({ ...it, timePublish: dayjs(it.timePublish, "YYYY-MM-DD HH:mm Z").fromNow() }));
}

