export async function getWhitelistedIdo() {
  const res = await axios({
    method: "get",
    url: `${config.apiEndpointWhitelistedIdo}`,
    params: {
      sort: "-createdAt",
      limit: 100
    }
  })

  return Promise.all(
    res.data.rows.map(async (metadata) => {
      if (!metadata.version) {
        const ido = await getIDOv1ByAddress(metadata.chainId, metadata.contract);
        return { ...ido, metadata }
      }

      if (metadata.version == "2") {
        console.log("getIDOv2ByAddress")
        const ido = await getIDOv2ByAddress(metadata.chainId, metadata.contract);
        return { ...ido, metadata }
      }
    })
  )
}

export async function getRoundInfo(chainId, idoName, roundIndex) {
  const res = await axios({
    method: "get",
    url: `${config.apiEndpointWhitelistedIdo}/${idoName}`,
    params: {
      chainId
    }
  })

  const resWhitelist = await axios({
    method: "get",
    url: res.data.rounds[roundIndex].whitelist
  })

  return {
    whitelist: resWhitelist.data
  }
}

export async function getIdoByName(chainId, idoName) {
  const res = await axios({
    method: "get",
    url: `${config.apiEndpointWhitelistedIdo}/${idoName}`,
    params: {
      chainId
    }
  })

  if (!res.data.version) {
    const ido = await getIDOv1ByAddress(chainId, res.data.contract);
    return { ...ido, metadata: res.data }
  } else if (res.data.version == "2") {
    const ido = await getIDOv2ByAddress(chainId, res.data.contract);
    return { ...ido, metadata: res.data }
  }
}

export function unixToDate(unixTimeStamp, format = "HH:mm MMM DD YYYY UTC") {
  return dayjs.unix(unixTimeStamp).utc().format(format)
}