import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  // {
  //   path: '/swap',
  //   name: 'Swap',
  //   component: () => import(/* webpackChunkName: "about" */ '../views/Swap.vue'),
  //   pathToRegexOptions: { strict: false }
  // },
  {
    path: '/pad',
    name: 'Pad',
    component: () => import(/* webpackChunkName: "about" */ '../views/LaunchPad.vue'),
    children: [
      {
        path: '/',
        component: () => import(/* webpackChunkName: "about" */ '../components/launchpad/AllPool.vue'),
        meta: {
          transitionName: 'fade',
          keepAlive: true
        }
      },
      {
        path: 'portfolio',
        component: () => import(/* webpackChunkName: "about" */ '../components/launchpad/v1/Portfolio.vue'),
        meta: {
          transitionName: 'fade',
          keepAlive: true
        }
      },
      {
        path: 'v1/:poolChainId/:idoName',
        component: () => import(/* webpackChunkName: "about" */ '../components/launchpad/v1/PoolDetail.vue'),
        meta: {
          transitionName: 'fade',
          keepAlive: false
        }
      },
      {
        path: 'v2/:poolChainId/:idoName',
        component: () => import(/* webpackChunkName: "about" */ '../components/launchpad/v2/PoolDetail.vue'),
        meta: {
          transitionName: 'fade',
          keepAlive: false
        }
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 }
  }
})

export default router
