<script>
export default {
  data() {
    return {
      showMenu: false,
    };
  },
  computed: {
    currentRoutePath() {
      return this.$route.fullPath;
    },
    currentRouteName() {
      const routeName = this.$route.fullPath.split("/")[1];
      return routeName.charAt(0).toUpperCase() + routeName.slice(1);
    },
  },
};
